#testimonialContainer{
    background-color: #e0e0e0;
    padding-top:30px;
    padding-bottom:30px;
}

.testimonialCard .productName{
    font-weight:600;
    font-size:16px;
    text-align: center;
    margin-bottom:0.75rem;
}

.testimonialCard p{
    text-align: center;
}

.testimonialCard .reviewer{
    display: block;
    text-align: center;
    font-weight:600;
}

.testimonialCard .location{
    font-weight:400;
    display: block;
    text-align: center;
    font-size:14px;

}

#testimonialContainer .swiper-slide {
    transition:all 200ms linear;
    transform: scale(0.8) !important;
  }
  #testimonialContainer  .swiper-slide-next{
    transform: scale(1) !important;
  } 

  #testimonialContainer .swiper{
    position:relative;
    z-index:0;
  }

  #testimonialContainer .slidePrev-btn{
    position:absolute;
    left:0%;
    top:40%;
    z-index:99;
    font-size:20px;
  }

  #testimonialContainer .slideNext-btn{
    position:absolute;
    right:0%;
    top:40%;
    z-index:99;
    font-size:20px;
  }

  @media screen and (max-width:720px){
    #testimonialContainer  .swiper-slide-active{
        transform: scale(1) !important;
      }
      #testimonialContainer .slidePrev-btn, #testimonialContainer .slideNext-btn{
        display: none;
      }  
  }