/* the parent */
/* #fragranceSlider  .slick-list {
    margin: 0 -27px;
} */

.fragranceSlider{
    position:relative;
    z-index:0;
}

.fragranceCard img{
    width:100%;
    height:45vh !important;
}

.fragranceCard .card{
    border:none;
}

.fragranceCard img{
    border-radius:5px;
}

.fragranceslidePrev-btn{
    position: absolute;
    z-index:99;
    top:40%;
    left:1%;
    font-size:30px;
}

.fragranceslideNext-btn{
    position: absolute;
    z-index:99;
    top:40%;
    right:1%;
    font-size:30px;
}

.categoryCard{
    height:auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-weight:600;
    font-size:20px;
    overflow: hidden;
}

.categoryCard .cardImage{
    height:47.6190vh;
    width:100%;
    overflow: hidden;
}

.categoryCard img{
    height:100%;
    width:100%;
    border-radius:5px;
}

.categoryCard span{
    font-size:16px;
    font-weight:500;
}

.title{
    padding-left:10px;
    font-weight: 600;
    color: #000;
}

#homePage .title{
    padding:0px 12px;
    padding-bottom:5px;
    margin-top:24px;
    /* font-family: 'FuturaCondensed' !important; */
    font-family: 'TabarroPro' !important;
    background:#fff;
    /* box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px; */
}

#homePage .title.fragrancesFont{
    font-family: 'TabarroPro' !important;
}

#homePage .productCount{
position:relative;
display: flex;
align-items: center;
justify-content: center;
}

#homePage .productCountData{
    position:absolute;
    z-index:99;
    font-weight: 600;
    color: #fff;
    font-size: 140px;
}

.bestSeller {
    /* background:url(../../assets/images/homePageBackGroundTexture.jpg); */
    /* background:url(../../assets/images/4My-Story.jpg); */
    background-repeat: no-repeat;
    background-size: cover;
}

.bannerSlider{
    position:relative;
    z-index:0;
}

.bannerSlider .slidePrev-btn{
    position:absolute;
    z-index:99;
    top:45%;
    left:3%;
    font-size:24px;
}

.bannerSlider .slideNext-btn{
    position:absolute;
    z-index:99;
    top:45%;
    right:3%;
    font-size:24px;
}

/* .videoContainer{
    padding:0 30px;
} */

.videoContainer .card{
    border-radius:10px;
}

.videoCard{
    position:relative;
    z-index:0;
}

.videoslidePrev-btn{
    position:absolute;
    left:0%;
    top:45%;
    z-index:100;
    font-size: 20px;
}

.videoslideNext-btn{
    position:absolute;
    right:0%;
    top:45%;
    z-index:100;
    font-size: 20px;
}

.videoSlider .card{
    position:relative;
    cursor: pointer;
    width:400px;
    height:200px;
}

.videoContainer .card:hover svg{
    transform: translate(0%,0%) scale(.85);
}

.videoSlider img{
    border-radius:10px;
}

.videoSlider .card svg{
    position:absolute;
    z-index:99;
    left:42%;
    top:36%;
    color:#fff;
    height:70px;
    width:70px;
}

.modalClose{
    background:#000;
    opacity:1;
    color:#fff;
    z-index:10000;
    padding:10px;
    position:absolute;
    right:-7px;
    top:-7px;
    border-radius:50%;
    font-size:12px;
    font-weight:600;
}

.modalClose:hover{
    opacity:1;
    background:#000;
}

.videoModal .modal-content{
    width:auto;
}

.partnerImg{
    text-align: center;
}

.partnerImg img{
    height:45px;
    width:120px;
}



@media screen and (max-width:992px){
    .fragranceCard img {
        width: 175px;
        height: 150px !important;
    }

    .fragranceslidePrev-btn, .fragranceslideNext-btn {
        top:30%;
    }
    .partnerImg img {
        height: 70px;
        width: 180px;
    }
    .videoSlider .card{
        width:auto;
    }

    .categoryCard .cardImage {
        height:30.619vh;
    }
}


@media screen and (max-width:720px){
    .uspBanner{
        display: block;
        font-size:18px;
    }
    .firstSecCol{
        margin-bottom:10px;
    }
    .categoryCard{
        height:auto;
    }
    .categoriesContainer{
        /* background:url(../../assets/images/new.jpg); */
        background-repeat: none;
        background-size:cover;
    }

    .mobile_fragrance .card img{
        height:150px;
        width:100%;
        border-radius:5px;
    }

    .mobile_fragrance .card{
        height:200px;
        border:none;
    }

    .mobile_fragrance .card .card-body{
        padding:0px;
        padding-top:7px;
    }

    .bestSeller {
        /* background:url(../../assets/images/homePageBackGroundTexture.jpg); */
        /* background:url(../../assets/images/4My-Story.jpg); */
