        background-size: cover;
    }

    .mobileJustLaunched {
        /* background:url(../../assets/images/homePageBackGroundTexture.jpg); */
        background-size:contain;
    }
    .mobile_fragrance{
        /* background:url(../../assets/images/1My-Dream.jpg); */
        background-repeat:none;
    }

    .categoryCard .cardImage {
        height: 15.619vh;
        width: 100%;
        overflow: hidden;
    }

    .mainBannerSlicker img {
        height: 100%;
    }

    .mobile_fragrance .card{
        box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    }

    .bannerSlider .slideNext-btn {
        top:40%;
        right:5%;
        font-size:20px;
    }

    .bannerSlider .slidePrev-btn {
        top:40%;
        left:5%;
        font-size:20px;
    }


    #homePage .productCountData{
        font-size: 50px;
    }

    .videoModal iframe{
        width:100%;
    }

    .videoModal .modal-content {
        width: 100%;
    }

    .videoSlider .card {
        width: 100%;
        height: 200px;
        border-radius:10px;
    }

    .videoslidePrev-btn {
        left: 0%;
    }

    .videoslideNext-btn {
        right: 0%;
    }

    .videoCard {
        padding-right: 20px;
        padding-left: 20px;
    }

    .partnerImg img{
        height:45px;
        width:120px;
    }

}

